import { Header } from "../Header/Header";
import { Footer } from "../Footer/Footer";
import { EventPage } from "../EventPage/EventPage";
import { EventListingsPage } from "../EventListingsPage/EventListingsPage";
import { About } from "../About/About";
import { AccountPage } from "../AccountPage/AccountPage";
import { HomePage } from "../HomePage/HomePage";
import { Restaurants } from "../Dining/Restaurants";
import { Auth0Provider } from "@auth0/auth0-react";
import runtimeEnv from "@mars/heroku-js-runtime-env";

import "./Layout.scss";
import "../assets/images/bg.jpg";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
// import { SignInRegistration } from "../SignInRegistration/SignInRegistration";

const env = runtimeEnv();

export const Layout = (props) => {
  return (
    <Router>
      <div className="Layout">
        <Header />

        <Switch>
          <Route path="/events/:event">
            <EventPage />
          </Route>
          <Route path="/about">
            <About />
          </Route>
          <Route path="/events">
            <EventListingsPage />
          </Route>
          {/* <Route path="/signin">
            <SignInRegistration />
          </Route> */}
          <Route path="/restaurants">
            <Restaurants />
          </Route>

          <Route path="/account">
            <Auth0Provider
              domain="hellosyr.us.auth0.com"
              clientId="9QtaZGHlzb3fhbBvkAve9Mrmuv1jpYef"
              redirectUri={env.REACT_APP_FRONTEND_URL + "/account"}
              scope="read:current_user"
            >
              {/* <Auth0ProviderWithHistory> */}
              <AccountPage />
              {/* </Auth0ProviderWithHistory> */}
            </Auth0Provider>
          </Route>

          <Route path="/">
            <HomePage />
          </Route>
        </Switch>
        <Footer />
      </div>
    </Router>
  );
};
